<h1 id="oauth" tabindex="-1"><a class="header-anchor" href="#oauth" aria-hidden="true">#</a> OAuth</h1>
<p>第三方服务商网页授权有两种：</p>
<ul>
<li><a href="https://open.work.weixin.qq.com/api/doc/90001/90143/91120#%E6%9E%84%E9%80%A0%E7%AC%AC%E4%B8%89%E6%96%B9%E5%BA%94%E7%94%A8oauth2%E9%93%BE%E6%8E%A5" target="_blank" rel="noopener noreferrer">第三方应用网页授权<OutboundLink/></a></li>
<li><a href="https://open.work.weixin.qq.com/api/doc/90001/90143/91120#%E6%9E%84%E9%80%A0%E4%BC%81%E4%B8%9Aoauth2%E9%93%BE%E6%8E%A5" target="_blank" rel="noopener noreferrer">企业网页授权<OutboundLink/></a></li>
</ul>
<p>创建实例：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token keyword">use</span> <span class="token package">EasyWeChat<span class="token punctuation">\</span>work<span class="token punctuation">\</span>Application</span><span class="token punctuation">;</span>

<span class="token variable">$config</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
    <span class="token string single-quoted-string">'corp_id'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'xxxxxxxxxxxxxxxxx'</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'secret'</span>   <span class="token operator">=></span> <span class="token string single-quoted-string">'xxxxxxxxxx'</span><span class="token punctuation">,</span> <span class="token comment">// 应用的 secret</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$app</span> <span class="token operator">=</span> <span class="token keyword">new</span> <span class="token class-name">Application</span><span class="token punctuation">(</span><span class="token variable">$config</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br></div></div><h2 id="获取-oauth-模块实例" tabindex="-1"><a class="header-anchor" href="#获取-oauth-模块实例" aria-hidden="true">#</a> 获取 OAuth 模块实例</h2>
<p>请根据你的场景选择对应的方法获取 OAuth 实例：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token comment"># 第三方应用网页授权</span>
<span class="token variable">$oauth</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token function">getOAuth</span><span class="token punctuation">(</span><span class="token keyword type-hint">string</span> <span class="token variable">$suiteId</span><span class="token punctuation">,</span> <span class="token class-name type-declaration">AccessTokenInterface</span> <span class="token variable">$suiteAccessToken</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token comment"># 企业网页授权</span>
<span class="token variable">$oauth</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token function">getCorpOAuth</span><span class="token punctuation">(</span><span class="token keyword type-hint">string</span> <span class="token variable">$corpId</span><span class="token punctuation">,</span> <span class="token keyword type-hint">int</span> <span class="token variable">$agentId</span><span class="token punctuation">,</span> <span class="token class-name type-declaration">AccessTokenInterface</span> <span class="token variable">$suiteAccessToken</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br></div></div><h2 id="跳转授权" tabindex="-1"><a class="header-anchor" href="#跳转授权" aria-hidden="true">#</a> 跳转授权</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token comment">// $callbackUrl 为授权回调地址</span>
<span class="token variable">$callbackUrl</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'https://xxx.xxx'</span><span class="token punctuation">;</span> <span class="token comment">// 需设置可信域名</span>

<span class="token comment">// 返回授权跳转链接</span>
<span class="token variable">$redirectUrl</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token function">getOAuth</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">redirect</span><span class="token punctuation">(</span><span class="token variable">$callbackUrl</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br></div></div><h2 id="获取授权用户信息" tabindex="-1"><a class="header-anchor" href="#获取授权用户信息" aria-hidden="true">#</a> 获取授权用户信息</h2>
<p>在回调页面中，你可以使用以下方式获取授权者信息：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$code</span> <span class="token operator">=</span> <span class="token string double-quoted-string">"回调URL中的code"</span><span class="token punctuation">;</span>
<span class="token variable">$user</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token function">getOAuth</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">detailed</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">userFromCode</span><span class="token punctuation">(</span><span class="token variable">$code</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token comment">// 获取用户信息</span>
<span class="token variable">$user</span><span class="token operator">-></span><span class="token function">getId</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span> <span class="token comment">// 对应企业微信英文名（userid）</span>
<span class="token variable">$user</span><span class="token operator">-></span><span class="token function">getRaw</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span> <span class="token comment">// 获取企业微信接口返回的原始信息</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br></div></div><p>📖 OAuth 详情请参考：<RouterLink to="/6.x/common/oauth.html">网页授权</RouterLink></p>
<p>获取用户其他信息需调用通讯录接口，参考：<a href="https://github.com/EasyWeChat/docs/blob/master/wework/contacts.md" target="_blank" rel="noopener noreferrer">企业微信通讯录API<OutboundLink/></a></p>
